  @media (min-width: 768px) {
    .comments {
      margin-top: 50px; } }
  .comments__title {
    font-size: 18px;
    line-height: 30px;
    font-weight: bold;
    color: #333333; }
    @media (min-width: 768px) {
      .comments__title {
        font-size: 20px; } }
  .comments__item {
    position: relative;
    margin-top: 20px;
    padding: 20px;
    border-radius: 5px;
    background-color: #EEF7FF; }
    @media (min-width: 768px) {
      .comments__item {
        padding: 30px 40px; } }
    .comments__item:nth-child(odd) {
      width: calc(100% - 60px);
      margin-left: auto; }
      .comments__item:nth-child(odd)::before {
        content: '';
        display: block;
        position: absolute;
        top: 30px;
        left: -16px;
        border: 8.5px solid transparent;
        border-top: 8.5px solid #EEF7FF;
        border-right: 8.5px solid #EEF7FF; }
      @media (min-width: 768px) {
        .comments__item:nth-child(odd) {
          width: calc(100% - 180px); } }
  .comments__top, .comments__answer {
    display: flex; }
  .comments__name {
    font-size: 15px;
    line-height: 19px;
    font-weight: bold;
    color: #333333; }
  .comments__date {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    color: #B6B6B6;
    margin-left: 20px; }
  .comments__text {
    font-size: 14px;
    line-height: 22px;
    font-weight: 500;
    color: #333333;
    margin: 20px 0; }
  .comments__answer svg {
    width: 20px;
    height: 20px;
    margin-right: 10px; }
  .comments__answer span {
    font-size: 14px;
    line-height: 18px;
    font-weight: bold;
    color: #0E3770;
    cursor: pointer; }

.popup-comment {
  padding: 40px 50px;
  border-radius: 5px; }
  .popup-comment__icon {
    text-align: center; }
    .popup-comment__icon svg {
      width: 50px;
      height: 42px;
      fill: #0E3770; }
  .popup-comment__thanks {
    width: 100%;
    max-width: 265px;
    font-size: 18px;
    line-height: 23px;
    font-weight: bold;
    color: #333333;
    text-align: center;
    margin-top: 40px; }

.fancybox-is-open .fancybox-bg {
  opacity: 0.5;
  background: #0E3770; }

.fancybox-close-small {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: #0E3770;
  border-radius: 5px; }
  .fancybox-close-small svg {
    fill: #fff; }

.sections .category-label {
  color: #333333;
  padding: 0;
  font-size: 17px;
  line-height: 21px;
  font-weight: bold;
  margin-bottom: 30px; }
  @media (min-width: 768px) {
    .sections .category-label {
      font-size: 25px;
      line-height: 30px;
      margin-bottom: 50px; } }
  .sections .category-label svg {
    width: 18px;
    height: 18px; }

.sections .lastest-events-block {
  display: block; }

.sections .lastest-events-item {
  display: block; }
  .sections .lastest-events-item:not(:first-child) {
    margin-top: 50px; }
    @media (min-width: 768px) {
      .sections .lastest-events-item:not(:first-child) {
        flex-direction: row; } }
  .sections .lastest-events-item:last-child .news__more {
    margin-bottom: 50px; }

.sections .lastest-events-content {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .sections .lastest-events-content {
      flex-direction: row; } }

.sections .hp-default-layout__main .news__banner {
  display: none; }
  @media (min-width: 1024px) {
    .sections .hp-default-layout__main .news__banner {
      display: block;
      margin-top: 50px; } }

.sections .news__breadcrumbs {
  margin: 20px 0 50px; }
  @media (min-width: 1024px) {
    .sections .news__breadcrumbs {
      margin: 30px 0 70px; } }

@media (min-width: 1024px) {
  .sections .news__right-side {
    margin-top: 80px; } }

.section-hp-main--searching-results {
  padding-top: 33px; }

.search-results-heading {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  font-size: 35px;
  line-height: 43px;
  margin-top: 70px;
  color: #b6b6b6; }
  .search-results-heading span {
    color: #333333; }

.hp-news-item--searching {
  position: relative; }
  .hp-news-item--searching:first-child {
    padding-top: 0; }
  .hp-news-item--searching:not(:first-child) .hp-news-item-post-type {
    top: 50px; }

.hp-news-item-post-type {
  position: absolute;
  right: 0;
  top: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  padding: 4px 20px;
  border: 2px solid #EEF7FF;
  box-sizing: border-box;
  border-radius: 3px;
  color: #0E3770;
  width: 93px;
  height: 30px;
  box-sizing: border-box; }

.news-choise--searching .news-choise__title {
  margin-bottom: 30px; }

@media screen and (max-width: 1023px) {
  .news__banners-top.news__banners-top--searching {
    margin-bottom: 28px; } }

@media screen and (max-width: 767px) {
  .search-results-heading {
    font-size: 20px;
    line-height: 24px;
    margin-top: 51px; }
  .hp-news-item-post-type {
    position: static;
    width: 100%;
    margin-top: 10px; } }
